import Immutable from "seamless-immutable";
import { createActions, handleActions } from "redux-actions";
import { createSelector } from "reselect";

export const key = "counter";

export const { decrement, increment } = createActions({
  DECREMENT: null,
  INCREMENT: null
});

const getState = state => state[key];
export const getCount = createSelector(getState, ({ count }) => count);

export const reducer = handleActions(
  {
    [decrement]: state => state.update("count", count => count - 1),
    [increment]: state => state.update("count", count => count + 1)
  },
  Immutable({ count: 0 })
);



